.social-share {
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-shrink: 0;
  @include tablet {
    padding-left: 2rem;
  }
  @include desktop {
    padding-left: 2rem;
  }
}

.social-share h4 {
  display: inline-block;
}

.social-share a {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 250ms ease-in-out;
}

.social-share svg {
  // padding-top: 5px;
  width: 100%;
  min-height: 17px;
  min-width: 17px;
  max-width: 17px;
  max-height: 17px;
  path {
    fill: #fff;
    transition: all 200ms ease-in-out;
  }
}

.social-share--list {
  list-style-type: none;
  display: inline-block;
  // padding-left: 1rem;

  > li {
    display: inline-block;
    margin: 0 0.25rem;
  }

}

.twitter-icon {
  a {
    background-color: #00aced;
  }
  &:hover a {
    background-color: lighten(#00aced, 15%);
  }
}

.linkedin-icon {
  svg {
    min-height: 15px;
    min-width: 15px;
    max-width: 15px;
    max-height: 15px;
  }
  a {
    background-color: #007bb6;
  }
  &:hover a {
    background-color: lighten(#007bb6, 15%);
  }
}

.facebook-icon {
  a {
    background-color: #3b5998;
  }
  &:hover a {
    background-color: lighten(#3b5998, 20%);
  }
}